@import '../scss/variables';

/*---------- Header-styles ----------*/

/*Header-Color*/

.color-header {
  .main-header-center .form-control {
    color: $white;
    &::placeholder {
      color: $white !important;
      opacity: 0.3;
    }
  }
  .hor-header .header-brand1 {
    margin-top: 4px;
  }
}

.color-header {
  .main-header-center .form-control::placeholder {
    color: $white !important;
    opacity: 0.3;
  }
  .app-header,
  .hor-header {
    background: $primary-1;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  }
  .app-header .header-right-icons .nav-link.icon {
    background: $black-1;
  }
  .app-header .header-right-icons .nav-link.icon:hover {
    background: $black-1 !important;
  }
  .app-sidebar__toggle:hover {
    color: #fff;
  }

  .nav-link.toggle,
  .header-right .nav-link.icon {
    background: $black-1;
    box-shadow: rgba(4, 4, 7, 0.1) 0px 2px 3px;
  }

  .animated-arrow {
    background: none;

    span {
      background: #e7e7f7;

      &:before,
      &:after {
        background: #e7e7f7;
      }
    }
  }
  &.active .animated-arrow span {
    background-color: transparent;
  }
  .header .nav-link.icon i,
  .header-right .nav-link.icon:hover i {
    color: #fff !important;
  }

  .header {
    .nav-link {
      color: #e7e7f7 !important;

      &:hover {
        color: $white;
      }
    }

    .form-inline .form-control {
      border: 1px solid $white-05 !important;
      background: rgba(0, 0, 0, 0.08);
      color: $white;
    }
  }
  .header .form-inline {
    .btn i {
      color: $white !important;
      opacity: 0.5;
    }

    .form-control::placeholder {
      color: $white !important;
      opacity: 0.3;
    }
  }
  .app-sidebar__toggle {
    background: rgba(0, 0, 0, 0.08);
    color: #fff;
    box-shadow: rgba(4, 4, 7, 0.1) 0px 2px 3px;
    a {
      color: $white;
    }
  }
  .nav-link.icon {
    background: rgba(0, 0, 0, 0.08);
    box-shadow: rgba(4, 4, 7, 0.1) 0px 2px 3px;
  }
}

@media (min-width: 992px) {
  .color-header {
    .hor-header.header .header-brand .header-brand-img {
      &.desktop-lgo {
        display: none;
      }
      &.dark-logo {
        display: block;
        margin-top: 5px;
      }
    }
  }
}
@media (max-width: 767px) {
  .color-header {
    .hor-header.header .header-brand-img {
      &.mobile-logo {
        display: none !important;
      }
      &.darkmobile-logo {
        display: block;
      }
    }
  }
}
@media (max-width: 1024px) and (min-width: 768px) {
  .color-header {
    .hor-header.header .header-brand-img {
      &.desktop-lgo,
      &.mobile-logo {
        display: none !important;
      }
      &.dark-logo {
        display: block !important;
        margin-top: 5px;
      }
    }
  }
}
@media (max-width: 767px) {
  .color-header {
    .app-header .header-brand1 .header-brand-img.light-logo1 {
      display: none !important;
    }
    .app-header .header-brand1 .header-brand-img.desktop-logo {
      display: block !important;
    }
  }
}
.dark-mode.color-header {
  .app-header,
  .hor-header {
    background: $primary-1;
  }

  .header .nav-link.icon {
    background: rgba(0, 0, 0, 0.08);
    box-shadow: rgba(4, 4, 7, 0.1) 0px 2px 3px;
  }

  .header .form-inline .form-control {
    border: 1px solid $white-05 !important;
    color: $white;
  }
}

/*Header-dark*/

.dark-header {
  .app-header,
  .hor-header {
    background: #17263a;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  }
  .hor-header .header-brand1 {
    margin-top: 4px;
  }

  .nav-link.toggle,
  .header-right .nav-link.icon {
    background: rgba(255, 255, 255, 0.05);
  }

  .animated-arrow {
    background: none;

    span {
      background: #e7e7f7;

      &:before,
      &:after {
        background: #e7e7f7;
      }
    }
  }
  &.active .animated-arrow span {
    background-color: transparent;
  }
  .header.hor-header .nav-link.icon,
  .header-right-icons .nav-link.icon:hover {
    color: #e7e7f7 !important;
    background: #1f324c;
  }

  .app-header.header .nav-link:hover {
    color: $white;
  }
  .app-header .header-right-icons .nav-link.icon {
    background: #1f324c;
  }
  .app-header .header-right-icons .nav-link.icon:hover {
    background: #1f324c !important;
  }
  .app-sidebar__toggle:hover {
    color: #fff;
  }

  .header .main-header-center .form-control {
    border: 1px solid $white-05 !important;
    background: #1f324c;
    color: $white;
  }

  .header .form-inline {
    .btn i {
      color: $white;
      opacity: 0.5;
    }

    .form-control::placeholder {
      color: $white;
      opacity: 0.3;
    }
  }

  .header.hor-header .header-brand-img {
    &.desktop-logo {
      display: none;
    }

    &.light-logo {
      display: block;
    }
  }
  .app-sidebar__toggle {
    background: #1f324c;
    color: #fff;
    a {
      color: $white;
    }
  }
  .nav-link.icon i {
    color: $white;
  }
}
@media (max-width: 768px) {
  .dark-header {
    .app-header .header-brand1 .header-brand-img {
      &.light-logo1 {
        display: none !important;
        margin-top: 5px;
      }
      &.desktop-logo {
        display: block;
      }
    }
    .navresponsive-toggler span {
      background: #1f324c !important;
      color: #fff !important;
    }
  }
}
@media (min-width: 992px) {
  .dark-header {
    .hor-header.header .header-brand .header-brand-img {
      &.desktop-lgo {
        display: none;
      }
      &.dark-logo {
        display: block;
        margin-top: 5px;
      }
    }
  }
}
@media (max-width: 768px) {
  .dark-header .hor-header.header .header-brand-img.desktop-logo-1 {
    display: none !important;
  }
}
@media (max-width: 1024px) and (min-width: 768px) {
  .dark-header {
    .hor-header.header .header-brand-img {
      &.desktop-lgo,
      &.mobile-logo {
        display: none !important;
      }
      &.dark-logo {
        display: block !important;
        margin-top: 5px;
      }
    }
  }
}

.light-mode.dark-header .hor-header .mobile-header.hor-mobile-header {
  background: #30304d !important;
}

/*Header-light*/

.dark-mode.light-header {
  .app-header,
  .hor-header {
    background: $white;
    border-bottom: 1px solid rgb(236, 240, 250);
  }
  .app-sidebar {
    box-shadow: none;
  }
  .header .nav-link.icon,
  .header-right .nav-link.icon {
    background: #ffffff;
    box-shadow: rgba(4, 4, 7, 0.1) 0px 2px 3px;
  }
  .animated-arrow {
    span {
      background: #5c678f;

      &:before,
      &:after {
        background: #5c678f;
      }
    }
  }
  &.active .animated-arrow span {
    background-color: transparent;
  }
  .header .form-inline .form-control {
    border: 1px solid transparent !important;
    background: #f1f5ff;
    color: $black;
  }

  .hor-header .header-brand-img.desktop-logo-1 {
    display: none;
  }

  .drop-profile a {
    color: #171a29;
  }

  .hor-header .header-brand-img.desktop-logo {
    display: block;
  }
  .header .nav-link.icon i {
    color: #17263a;
  }
  .app-sidebar__toggle {
    background: #fff;
    box-shadow: rgba(4, 4, 7, 0.1) 0px 2px 3px;
    a {
      color: #555b95;
    }
  }
}
@media (max-width: 575.98px) {
  .dark-mode.light-header .header .form-inline .form-control {
    background: #191d43;
  }
}
@media (max-width: 767px) {
  .dark-mode.light-header {
    .hor-header .header-brand-img.desktop-logo {
      display: none;
    }
    .app-header .header-brand1 .header-brand-img {
      &.desktop-logo {
        display: none;
      }
      &.light-logo1 {
        display: block !important;
      }
    }
    .mobile-header {
      background-color: #ffffff;
    }
    .mobile-header .header-brand-img.desktop-logo {
      display: none !important;
    }
    .mobile-header .header-brand-img.desktop-logo.mobile-light {
      display: block !important;
    }
  }
}
@media (max-width: 768px) and (min-width: 568px) {
  .app.dark-mode.light-header {
    .app-header .header-brand .header-brand-img {
      &.desktop-lgo {
        display: block;
        margin-top: 5px;
      }
      &.dark-logo,
      &.mobile-logo,
      &.darkmobile-logo {
        display: none !important;
      }
    }
  }
}
@media (max-width: 1024px) and (min-width: 768px) {
  .dark-mode.light-header {
    .hor-header.header .header-brand-img {
      &.dark-logo,
      &.mobile-logo {
        display: none !important;
      }
      &.desktop-lgo {
        display: block !important;
        margin-top: 5px;
      }
    }
  }
}

