/*---------- Leftmenu-styles ----------*/

/*Light-menu*/
.light-menu {
  .app-sidebar {
    background: $white;
    .app-sidebar__user {
      border-bottom: $border;
    }
    .side-menu .slide a {
      color: #9eabc0;
    }
    .side-menu .sidemenu_icon {
      color: #fff !important;
    }
    .app-sidebar__user {
      .user-pic .avatar-xxl {
        border: 3px solid #ebeff8;
      }
      .user-pic::before {
        border: 2px solid #fff;
      }
      .user-info h5 {
        color: #263871;
      }
    }
    .Annoucement_card {
      background: #ebeff8;
      .title,
      .text-white {
        color: #263871 !important;
      }
    }
  }
  .slide a.active .sidemenu_icon,
  .side-menu__item:hover .sidemenu_icon {
    color: $white;
  }
  .slide-menu li {
    &.active > a,
    &:hover > a {
      color: $primary-1 !important;
    }
  }
  .side-item.side-item-category {
    opacity: 0.7 !important;
  }
}
.dark-mode.light-menu {
  .app-sidebar {
    background: $white !important;
    .app-sidebar__user {
      border-bottom: $border;
    }
    .side-menu .slide a {
      color: #9eabc0;
    }
    .side-menu .sidemenu_icon {
      color: #9eabc0;
    }
    .app-sidebar__user {
      .user-pic .avatar-xxl {
        border: 3px solid #ebeff8;
      }
      .user-pic::before {
        border: 2px solid #fff;
      }
      .user-info h5 {
        color: #9eabc0;
      }
    }
    .Annoucement_card {
      background: #ebeff8;
      .title,
      .text-white {
        color: #263871 !important;
      }
    }
  }
  .slide a.active .sidemenu_icon,
  .side-menu__item:hover .sidemenu_icon {
    color: $white;
  }
  .slide-menu li {
    &.active > a,
    &:hover > a {
      color: $primary-1 !important;
    }
  }
}

@media (min-width: 768px) {
  .light-menu {
    .app-sidebar__logo {
      border-bottom: $border;
      border-right: $border;
    }
    .app-sidebar .app-sidebar__logo .header-brand-img {
      &.desktop-lgo {
        display: block;
        margin: 0 auto;
        margin-top: 5px;
      }
      &.dark-logo {
        display: none;
      }
    }
    &.sidenav-toggled {
      .app-sidebar .app-sidebar__logo .header-brand-img {
        &.desktop-lgo {
          display: none;
        }
      }
    }
    &.sidenav-toggled.sidenav-toggled1 {
      .app-sidebar .app-sidebar__logo .header-brand-img {
        &.desktop-lgo {
          display: block;
          margin: 0 auto;
          margin-top: 5px;
        }
        &.dark-logo {
          display: none;
        }
      }
    }
  }
  .dark-mode.light-menu {
    .side-header {
      border-bottom: 1px solid #eaedf1;
      border-right: 1px solid #eaedf1;
      top: -4px;
    }
    .side-menu h3 {
      color: #000;
    }
    .side-menu__item:hover,
    .side-menu__item:focus {
      background: #f9fbfd !important;
    }
    .side-menu .side-menu__icon,
    .dark-mode .side-menu__item,
    .dark-mode .slide-item {
      color: #6e84a3;
    }
    .side-menu__label {
      color: #6e84a3;
    }
    .side-menu__item.active:hover,
    .side-menu__item.active:focus {
      color: #ffffff;
      background: linear-gradient(
        to right,
        #9e88f5 0%,
        #6259ca 100%
      ) !important;
    }
    .side-menu__item.active .side-menu__icon {
      color: #ffffff !important;
    }
    .app-sidebar .side-header .header-brand-img {
      &.light-logo1 {
        display: block !important;
        margin: 0 auto;
        margin-top: 5px;
      }
      &.desktop-logo {
        display: none !important;
      }
    }
    &.sidenav-toggled {
      .app-sidebar .side-header .header-brand-img {
        &.desktop-logo {
          display: none !important;
        }
      }
    }
    &.sidenav-toggled.sidenav-toggled-open {
      .app-sidebar .side-header .header-brand-img {
        &.light-logo1 {
          display: block !important;
          margin: 0 auto;
          margin-top: 5px;
        }
        &desktop-logo {
          display: none !important;
        }
      }
    }
  }
}
.app.sidebar-mini.dark-mode.light-menu.sidenav-toggled.sidenav-toggled-open
  .app-sidebar
  .side-header
  .header-brand-img.desktop-logo {
  display: none !important;
}
.app.sidebar-mini.dark-mode.light-menu.sidenav-toggled.sidenav-toggled-open
  .app-sidebar
  .side-header
  .header-brand-img.light-logo1 {
  display: block !important;
}

/*Color-menu*/

.color-menu {
  .app-sidebar {
    border-right: 1px solid rgba(255, 255, 255, 0.1);
  }
  .app-sidebar .side-header {
    background: $primary-1;
